import React from "react";
import "./App.css";
import styled from "styled-components";

import FourAM from "./Media/438am.mp3";
import ShouldveKissed from "./Media/ShouldveKissed.mp3";
import headphoneMia from "./Media/HeadphoneMia.JPG";

import HowItStarted from "./Components/HowItStarted";

const SongButton = styled.button`
  width: auto;
  border: 0;
  margin: 2%;
  background-color: #ffffff00;
  // font-family: "Monaco", "Monaco", "monospace";
  //font-family: "Gill Sans", sans-serif;
  // font-family: monospace;
  font-family: Monaco, MonoSpace;
  font-size: 150%;
  font-weight: 600;
  color: #3d3b3b;
  width: 100%;
`;
const SongButtonSecond = styled(SongButton)`
  border-top: solid;
  padding-top: 3%;
`;

class App extends React.Component {
  fourAM = new Audio(FourAM);
  shouldveKissed = new Audio(ShouldveKissed);

  state = {
    songs: [this.fourAM, this.shouldveKissed],
    songSelected: this.fourAM,
    songPlay: true,
  };

  onPlay = () => {
    // new Audio(song).play();
    this.setState({
      songPlay: !this.state.songPlay,
    });

    console.log(this.state.songPlay);
    if (this.state.songPlay === true) {
      this.state.songSelected.play();
    } else {
      this.state.songs.forEach((song) => {
        song.pause();
      });
      //this.state.songSelected.pause();
    }
    //console.log(this.state);
  };

  onSelect = (song) => {
    this.state.songs.forEach((song) => {
      song.pause();
    });
    this.setState({
      songSelected: song,
      songPlay: false,
    });
    song.currentTime = 0;
    song.play();
  };

  render() {
    return (
      <div className="body">
        <div className="how-it-started">
          <HowItStarted></HowItStarted>
        </div>

        <div id="outter">
          <div onClick={this.onPlay}>
            <img
              className="App-logo"
              style={{
                width: "330px",
                height: "auto",
              }}
              src={headphoneMia}
              alt="headphoneMia"
            />
          </div>
          <div className="inner">
            <SongButton
              className="butt-hover"
              onClick={() => this.onSelect(this.fourAM)}
            >
              4:38am
            </SongButton>
            <SongButtonSecond
              className="butt-hover"
              onClick={() => this.onSelect(this.shouldveKissed)}
            >
              Should've kissed you
            </SongButtonSecond>
          </div>
        </div>
      </div>
    );
  }
}

export default App;
